.hero {
  position: relative; }
  .hero__text {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    position: relative;
    z-index: 1;
    text-align: center; }
  .hero h1 {
    margin: 0;
    max-width: 720px;
    font-size: 60px; }
  .hero p {
    opacity: 0.2;
    font-weight: 700; }
  .hero__particles {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0; }
    .hero__particles::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 10;
      background-image: linear-gradient(to bottom, #030303, #030303, transparent); }
    .hero__particles > div {
      width: 100%;
      height: 100%; }
