.title-bar {
  display: flex;
  justify-content: space-between;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  padding: 30px;
  z-index: 100; }
  .title-bar h5 {
    margin: 0;
    line-height: 1; }
  .title-bar p {
    margin: 3px 0 0;
    opacity: 0.3; }
  .title-bar section:last-child {
    text-align: right; }
